<template>
    <div class="login-container">
        <el-card class="login-card">
            <template #header>
                <h2>登录</h2>
            </template>
            <el-form :model="loginStore.form" ref="formRef">
                <el-form-item>
                    <el-input v-model="loginStore.form.username" placeholder="用户名"
                        :class="{ 'error-input': loginStore.loginError }" />
                </el-form-item>
                <el-form-item>
                    <el-input v-model="loginStore.form.password" placeholder="密码" type="password" />
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleSqlLogin">登录</el-button>
                    <el-button @click="toRegister">现在去注册</el-button>
                    <el-button id="githubLoginBtn" @click="handleGithubLogin">
                        
                        使用 GitHub 登录
                    </el-button>
                    <el-button @click="toHome">游客</el-button>
                </el-form-item>
            </el-form>
            <p>{{ loginStore.message }}</p>
        </el-card>
        <img src="/background.jpg" alt="背景图" class="bg-image" />
    </div>
</template>

        <script setup>
import { defineEmits, ref } from 'vue';
import { useLoginStore } from '../store/loginStore';
import { useRouter } from 'vue-router'; // 引入 useRouter 钩子

const loginStore = useLoginStore();
const emits = defineEmits(['login-success']);
const formRef = ref(null);
const router = useRouter();

const handleSqlLogin = async () => {
    const isSuccess = await loginStore.sqlLogin();
    if (isSuccess) {
        emits('login-success');
        window.location.href = '/home'; // 补全 `shop` 路径，与部署结构一致  
    }
};

const handleGithubLogin = () => {
    window.location.href = '/api/auth/github';
};

const toRegister = () => {
    router.push('/register'); // 使用 router.push 跳转
};
const toHome = () => {
    router.push('/home');
};
</script>

        <style scoped>
            .login-container {
                display: flex;
                justify-content: center;
                align-items: center;
                min-height: 100vh;
            }

            .login-card {
                width: 400px;
                padding: 24px;
                box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
            }

            .el-form-item {
                margin-bottom: 16px;
            }

            .login-container {
                position: relative;
                min-height: 100vh;
            }

            .bg-image {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: -1;
                /* 置于底层 */
            }

            .login-card {
                position: relative;
                z-index: 1;
                max-width: 400px;
                margin: 0 auto;
                padding: 2rem;
                background: white;
                /* 卡片背景色 */
            }
        </style>
    